.nav {
  width: 80%;
  padding: 8px;
  height: fit-content;
  background-color: #fff;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 20px;

  display: flex;
  flex-direction: row;
  justify-content: space-around;

  position: absolute;
  z-index: 1;
  bottom: 25px;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
}
